<script>
import axios from "axios";
import $ from "jquery";
export default {
  mounted() {
    this.width = window.innerWidth;
    console.log(this.width);
    this.search("law04");
  },
  watch: {
    searchLaw() {
      console.log(this.searchLaw);
      let dbTableName = "";
      this.searchContent = [];
      switch (this.searchLaw) {
        case "水污染防治法":
          dbTableName = "law01";
          break;
        case "水利法":
          dbTableName = "law02";
          break;
        case "水利法施行細則":
          dbTableName = "law03";
          break;
        case "水庫蓄水範圍使用管理辦法":
          dbTableName = "law04";
          break;
        case "水體水質監測站設置及監測準則":
          dbTableName = "law05";
          break;
        case "地下水管制辦法":
          dbTableName = "law06";
          break;
        case "自來水法":
          dbTableName = "law07";
          break;
      }
      this.search(dbTableName);
    },
  },
  data() {
    return {
      width: 0,
      buttonType: [
        "primary",
        "success",
        "info",
        "warning",
        "danger",
        "primary",
        "success",
      ],
      lawNameArray: [
        "水庫蓄水範圍使用管理辦法",
        "水體水質監測站設置及監測準則",
        "地下水管制辦法",
        "水利法施行細則",
        "水污染防治法",
        "水利法",
        "自來水法",
      ],
      searchLaw: "水庫蓄水範圍使用管理辦法",
      searchContent: [],
      input: "",
    };
  },
  methods: {
    search(dbTableName) {
      let dbcolumn = "COL 1";
      axios
        .post(`${this.allURL}law/lawSearch`, {
          lawName: dbTableName,
        })
        .then((res) => {
          console.log(res);
          res.data.forEach((item) => {
            this.searchContent.push(item[dbcolumn]);
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    searchKeyWords() {
      console.log(this.input);
      var isfound = 0;
      if (window.find && window.getSelection) {
        document.designMode = "on";
        isfound = 0;
        document.execCommand("HiliteColor", false, "white");
        while (window.find(this.input)) {
          $("#button").blur();
          document.execCommand("HiliteColor", false, "aquamarine");
          isfound = 1;
        }
        if (isfound == 0) {
          alert("未找到結果");
        }
        document.designMode = "off";
      }
    },
  },
};
</script>
